import React from 'react'
import DesignProjects from '../components/DesignProjects.js'
import Layout from "../components/layout"
import backIcon from '../images/close.png'
import AniLink from "gatsby-plugin-transition-link/AniLink"
import { graphql } from 'gatsby'

export default class ArtPage extends React.Component {
    render() {
        return <Layout>
            <div style={{ 
                display: 'flex', 
                alignItems: 'center', 
                marginTop: 24, 
                marginBottom: 24 
            }}>
                    <AniLink 
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }}
                        to="/" 
                        replace 
                        cover 
                        bg="#f5587b" 
                        direction="right"
                    >
                        <img style={{ margin: 0 }} src={ backIcon } alt="" />
                    </AniLink>
                    <h2 style={{ marginLeft: 24 }}>Art and Design Projects</h2>
                </div>
                <DesignProjects designs={ this.props.data.designs.edges }/>
            </Layout>
    }
}

export const query = graphql`
    fragment DesignFragment on Design {
        url 
        title 
        description
        image
    }

    query {
        designs: allDesign {
            edges {
                node {
                    ...DesignFragment
                }
            }
        }
    }
`
